import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { CollegePredictorPage } from './college-predictor.page';

const routes: Routes = [
  {
    path: '',
    component: CollegePredictorPage
  },
  {
    path: 'counselling-type/:examId',
    loadChildren: () => import('./counselling-type/counselling-type.module').then( m => m.CounsellingTypePageModule)
  },
  {
    path: 'predictor-form/:examId/:CounsellingType',
    loadChildren: () => import('./predictor-form/predictor-form.module').then( m => m.PredictorFormPageModule)
  }, 
  {
    path: 'predictor-checkout',
    loadChildren: () => import('./predictor-checkout/predictor-checkout.module').then( m => m.PredictorCheckoutPageModule)
  }, 
  {
    path: 'predictor-checkout/:type',
    loadChildren: () => import('./predictor-checkout/predictor-checkout.module').then( m => m.PredictorCheckoutPageModule)
  }, 
  {
    path: 'predictor-report',
    loadChildren: () => import('./predictor-report/predictor-report.module').then( m => m.PredictorReportPageModule)
  },
  {
    path: 'predictor-report-stagewise',
    loadChildren: () => import('./predictor-report-stagewise/predictor-report-stagewise.module').then( m => m.PredictorReportStagewisePageModule)
  },
  {
    path: 'personalized-report',
    loadChildren: () => import('./personalized-report/personalized-report.module').then( m => m.PersonalizedReportPageModule)
  },
  {
    path: 'preferential-form',
    loadChildren: () => import('./preferential-form/preferential-form.module').then( m => m.PreferentialFormPageModule)
  },
  {
    path: 'mbbs-foundation-course',
    loadChildren: () => import('./mbbs-foundation-course/mbbs-foundation-course.module').then( m => m.MbbsFoundationCoursePageModule)
  },
  {
    path: 'admission-guidance',
    loadChildren: () => import('./admission-guidance/admission-guidance.module').then( m => m.AdmissionGuidancePageModule)
  },
  {
    path: 'college-list/:managementId',
    loadChildren: () => import('./college-list/college-list.module').then( m => m.CollegeListPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class CollegePredictorPageRoutingModule {}
