import { parse } from 'path';
import { ToastService } from './../../services/toast.service';
import { ApiGeneralService } from './../../services/api-general.service';
import { LoaderService } from './../../services/loader.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ApiPredictorService } from 'src/app/services/api-predictor.service';
import { Component, OnInit } from '@angular/core';
import { HeaderTitleService } from 'src/app/services/header-title.service';
import { environment } from 'src/environments/environment.prod';
import { DomSanitizer } from '@angular/platform-browser';
import { FirebaseAnalytics } from '@ionic-native/firebase-analytics/ngx';

@Component({
  selector: 'app-college-predictor',
  templateUrl: './college-predictor.page.html',
  styleUrls: ['./college-predictor.page.scss'],
})
export class CollegePredictorPage implements OnInit {

  managementId = 0;
  arrExam = [];
  webAssetPath = environment.webAssetsPath;

  //#region Video Variables
  isVideoOpen : boolean ;
  iframe : any;  
  slideVideoOpts = { slidesPerView: 1.1 }
  arrVideos = [];
  webVideoPath = environment.webAssetsPath + "mobility-app/videothumbnails";
  assetpath = environment.webAssetsPath + "mobility-app/";
//#endregion

  arrReviews = [];
  webAssetsPathReviews= "";
  slideOpts = {slidesPerView: 1.2}

  constructor(
    private headerTitleService : HeaderTitleService,
    private apiPredictorService : ApiPredictorService,
    private router : Router,
    private apiGeneralService : ApiGeneralService,
    private sanitizer : DomSanitizer,
    private toastr: ToastService,
    private firebaseAnalytics : FirebaseAnalytics
  ) { 
    this.headerTitleService.isBack=false;
    this.headerTitleService.title = "College Predictor";    
  }
  ionViewWillEnter(){
    this.firebaseAnalytics.setCurrentScreen("college-predictor");
    this.firebaseAnalytics.logEvent("predictor_home", { 'resp': 'success', 'page': 'college-predictor'});
  }
  ngOnInit() {     
    this.webAssetPath = environment.webAssetsPath;  
    this.getExam();
    this.getVideos();
    this.getReview();
  }
  doRefresh(event) {
    this.getExam();
    this.getVideos();
    this.getReview();
    this.getVideos();
    setTimeout(() => {event.target.complete()}, 2000);
  }

  getExam(){        
    let postData = {'IsActive':true}
    this.apiPredictorService.getExamMaster(JSON.stringify(postData)).then(resp=>{
      this.arrExam = resp;     
    })
    .catch(ex => {
      console.log("getExam() => " + ex);      
    })
  }
  onClickExam(examId){
    this.router.navigate(['/home/college-predictor/counselling-type', examId]);
  }  

  //#region Video Section start
  getVideos() {
    let postData = { 'videoHeaderId': 24, 'isActive': true }
    this.apiGeneralService.getVideos(JSON.stringify(postData)).then(resp => {
      this.arrVideos = resp;
    }).catch(err => {
      console.log("getVideos: " + err);
    })
  }
  
  playVideo(videoSrc){
    if(videoSrc == ""){
      this.toastr.toastError("Sorry! Currently this video is not available.");
      return false;
    }
    this.iframe = this.sanitizer.bypassSecurityTrustHtml('<iframe src="'+videoSrc+'" frameborder="0" style="width: 100%; height: 300px;"allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>');    
    this.isVideoOpen = true;
  }
  videoPanelClose(){
    this.iframe = "";
    this.isVideoOpen = false;
  }
  //#endregion

  //#region Get Reviews
  getReview() {
    let postData = { "Category": "Predictor", "IsActive": true }
    this.apiGeneralService.getReviews(JSON.stringify(postData)).then(resp => {
      this.arrReviews = resp;      
      this.webAssetsPathReviews = environment.webAssetsPath + "mobility-app/student-review";  
    })
      .catch(err => {
        console.log("getReview: " + err);   
      })
  }
  //#endregion
}
